import React, { Component } from 'react'
import './leftbar.scss'
import { Icon } from 'antd'
import logo from '../../assets/img/logo.jpg'

const IconFont = Icon.createFromIconfontCN({
  scriptUrl: '//at.alicdn.com/t/font_1172057_htuda8fwgee.js',
});

export default class extends Component {
  // 渲染
  render() {
    return (
    	<div className="leftbar">
        <img src={logo} alt="" className="logo" />
	      <ul>
          <li>
            <div><Icon type="message" /></div>
            <p>公布消息</p>
          </li>
          <li>
            <div><Icon type="cloud-upload" /></div>
            <p>存储消息</p>
          </li>
          <li>
            <div><Icon type="cloud-download" /></div>
            <p>提取消息</p>
          </li>
          <li>
            <div><IconFont type="icon-wishlist" /></div>
            <p>祝福语</p>
          </li>
          <li>
            <div><Icon type="user" /></div>
            <p>关于我们</p>
          </li>
        </ul>
    	</div>
    );
  }
}